<template>
  <div class="field__item">
    <div class="row">
      <span class="prop">{{prop}}</span>
      <span class="type">{{parentType}}
        <span class="rec"
              v-if="parentType === 'rec'">
          {{$t('ref')}}:<code class="ref">{{field.$ref}}</code>
        </span>
      </span>
      <span class="required"
            v-if="!getRequired">{{$t('optional')}}</span>
      <span class="name">{{field.$name||field.name||field.$desc||field.desc||$t('none')}}</span>
      <span class="other__prop__list">
        <span class="other__prop"
              v-for="prop in otherProps"
              :key="prop.key">
          <span class="other__prop__key">{{prop.key}} </span>
          <span class="other__prop__value"> {{prop.value}}</span>
        </span>
      </span>
    </div>
    <complex-type :field="field"
                  v-if="isComplexType" />
  </div>
</template>

<script>
import ComplexType from './index.vue';
import { getParentType } from '../../../utils';

export default {
  name: 'field',
  components: {
    ComplexType,
  },
  props: {
    field: [Object, Array],
    prop: String,
  },
  data() {
    return {
      publicProps: ['name', 'desc', 'value', 'type', 'required', 'default', '$ref', '$type', '$name'],
      sortedProps: [
        'enum',
        'value', 'min_value', 'max_value',
        'length', 'min_length', 'max_length',
        'decimal_places', 'min_decimal_places', 'max_decimal_places',
        'count', 'min_count', 'max_count',
      ],
    };
  },
  computed: {
    isComplexType() {
      return Array.isArray(this.field)
        || ['map', 'object'].includes(this.field?.$type);
      // || this.field?.$enum;
    },
    getRequired() {
      return (typeof this.field === 'object' && 'required' in this.field) ? this.field.required : true;
    },
    parentType() {
      const field = (this.field?.$type === 'map' ? 'Map<Key, Value>' : this.field?.$type)
        || (this.field.$enum || this.field.enum ? 'enum' : '')
        || this.field.type
        || (Array.isArray(this.field) ? 'Array' : 'string');
      const parent = getParentType(field);
      return parent ? `${field} : ${parent}` : field;
    },
    otherProps() {
      const props = [];
      if (!this.isComplexType) {
        Object.keys(this.field)
          .filter(key => !this.publicProps.includes(key))
          .sort((a, b) => {
            const aKey = a.startsWith('$') ? a.slice(1) : a;
            const bKey = b.startsWith('$') ? b.slice(1) : b;
            const aKeyIndex = this.sortedProps.indexOf(aKey);
            const bKeyIndex = this.sortedProps.indexOf(bKey);
            return (aKeyIndex > -1 && bKeyIndex > -1) ? aKeyIndex - bKeyIndex : 1;
          }).forEach((key) => {
            let k = key.startsWith('$') ? key.slice(1) : key;
            const isEnum = key === '$enum' || key === 'enum';
            if (isEnum) {
              k = key;
            }
            props.push({
              key: k,
              value: this.field[key],
            });
          });
        return props;
      }
      return null;
    },
  },
};
</script>

