    border-radius: 4px;
    border: 1px solid #ccc;
    margin-left: 10px;
    color: #444;
    font-size: 0.9rem;
    padding: 2px;
  }

  .rec,
  .name {
    margin-left: 10px;
  }

  .rec {
    .ref {
      font-weight: 600;
      font-size: 1.2rem;
    }
  }

  .more {
    cursor: pointer;
    background-color: #ccc;
    color: white;
  }
}
.other__prop {
  margin: 0 5px;
