<template>
  <div class="object">
    <div class="title">{{title || $t('sub_property')}}</div>
    <template v-if="field">
      <div class="field"
           v-for="(field,prop) in useableFields"
           :key="prop">
        <Field :field="field"
               :prop="prop">
        </Field>
      </div>
    </template>
  </div>
</template>

<script>

export default {
  name: 'ObjectType',
  components: {
    Field: () => import('./Field.vue'),
  },
  props: {
    title: String,
    field: Object,
  },
  data() {
    return {
      publicProps: ['$name', '$desc', '$required', '$type', '$default', '$length', '$min_length', '$max_length', '$count', '$min_count', '$max_count'],
    };
  },
  computed: {
    useableFields() {
      const bd = {};
      Object.keys(this.field)
        .filter(key => !this.publicProps.includes(key))
        .forEach((key) => {
          bd[key] = this.field[key];
        });
      return bd;
    },
  },
};
</script>

