  border-radius: 5px 5px 0 0;
}

.field {
  padding: 2px 10px;
  border: 1px solid #ddd;
  border-top: none;
  font-size: 0.85rem;
  &:last-child {
    margin-bottom: 10px;
    border-radius: 0 0 5px 5px;
  }
}
</style>
