<template>
  <div class="array">
    <div v-if="type !== 'object'">
      <div class="title">{{defaultTitle}}</div>
      <div class="name">{{field.name}}</div>
    </div>
    <ComplexType :field="field"
                 :title="defaultTitle"
                 v-else />
  </div>
</template>

<script>
import { getParentType } from '../../../utils';

export default {
  name: 'ArrayType',
  components: {
    ComplexType: () => import('./index.vue'),
  },
  props: {
    prefixTitle: String,
    field: Object,
  },
  computed: {
    defaultTitle() {
      return `${this.prefixTitle ?? ''}${this.$t('array_member')} [${this.arrayType}]`;
    },
    arrayType() {
      const parent = getParentType(this.type);
      return parent ? `${this.type} : ${parent}` : this.type;
    },
    type() {
      if (this.field.$type) return this.field.$type;
      if ('type' in this.field && typeof (this.field.type) === 'string') {
        return this.field.type;
      }
      return 'string';
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  font-weight: 600;
  padding: 10px;
  background-color: #eee;
  border: 1px solid #ddd;
  border-bottom: none;
  border-radius: 5px 5px 0 0;
}

.name {
  padding: 10px;
  font-size: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 0 0 5px 5px;
  margin-bottom: 10px;
}
</style>
