<template>
  <div class="map">
    <div class="keyy">
      <div class="title">Key</div>
      <div class="name"><span class="key">{{mapKey}}</span>{{field.$key.name}}</div>
    </div>
    <div class="value">
      <div class="title">Value {{type}}</div>
      <div class="name">
        <ComplexType v-if="isObject || isEnum || isArray || isMap"
                     :field="field.$value" />
        <div v-else
             class="simple">
          <span class="key">{{field.$value.type}}</span>{{field.$value.name}}</div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  components: {
    ComplexType: () => import('./index.vue'),
  },
  props: {
    field: Object,
  },
  computed: {
    mapKey() {
      const key = this.field.$key.type || 'string';
      return key;
    },
    isArray() {
      return Array.isArray(this.field.$value);
    },
    isObject() {
      return this.field.$value?.$type === 'object';
    },
    isEnum() {
      return '$enum' in this.field.$value;
    },
    isMap() {
      return this.field.$value?.$type === 'map';
    },
    type() {
      if (this.isArray) return '(Array)';
      if (this.isObject) return '(Object)';
      if (this.isMap) return '(Map<Key, Value>)';
      if (this.isEnum) return '(Enum)';
      return '';
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  padding: 10px;
  background-color: #eee;
  border: 1px solid #ddd;
  border-bottom: none;
  border-radius: 5px 5px 0 0;
  font-weight: 600;

  &.value {
    border-top: 1px solid #ddd;
  }
}
.name {
  padding: 10px;
  font-size: 0.8rem;
  border: 1px solid #ddd;
  margin-bottom: 10px;
  border-radius: 0 0 5px 5px;

  .key {
    color: #aaa;
    margin-right: 10px;
  }
}

.type {
  margin-left: 5px;
}
</style>
