<template>
  <div class="type">
    <div class="row">
      <div class="title">{{title}}</div>
      <el-switch v-model="useRaw"
                 :active-text="$t('switch_raw')" />
    </div>
    <Code v-if="useRaw"
          :json="body"
          :title="title" />
    <template v-else>
      <div class="body"
           v-if="!(isSimpleType || isArray)">
        <div class="field"
             v-for="(field,prop) in useableBody"
             :key="prop">
          <Field :field="field"
                 :prop="prop" />
        </div>
      </div>
      <ArrayType v-else-if="isArray"
                 :field="body[0]">
      </ArrayType>
      <div class="simple-body"
           v-else-if="isSimpleType">
        <span class="name">{{body.name}}</span>
        <span class="desc">{{body.desc}}</span>
      </div>
      <div class="none"
           v-else>{{$t('none_body')}}</div>
    </template>
  </div>
</template>

<script>
import { Switch } from 'element-ui';
import Field from './ComplexType/Field.vue';
import ArrayType from './ComplexType/ArrayType.vue';
import Code from '../Code.vue';

export default {
  name: 'simple-type',
  components: {
    ArrayType,
    Field,
    Code,
    'el-switch': Switch,
  },
  props: {
    title: String,
    isObject: Boolean,
    body: [Object, Array],
  },
  watch: {
    '$route.query': {
      immediate: true,
      handler() {
        this.useRaw = false;
      },
    },
  },
  data() {
    return {
      useRaw: false,
      publicProps: ['$name', '$desc', '$required', '$type', '$default'],
    };
  },
  computed: {
    useableBody() {
      const bd = {};
      if (this.isObject
        || (typeof this.body === 'object' && this.body.$type === 'object')
      ) {
        Object.keys(this.body)
          .filter(key => !this.publicProps.includes(key))
          .forEach((key) => {
            bd[key] = this.body[key];
          });
      }
      return bd;
    },
    isArray() {
      return Array.isArray(this.body);
    },
    isSimpleType() {
      return typeof this.body === 'object' && typeof this.body.type === 'string';
    },
  },
};
</script>

<style lang="scss" scoped>
.type {
  .row {
    display: flex;
    align-items: center;
    border-bottom: 2px solid #ddd;
    margin-bottom: 10px;
    .title {
      padding: 10px 0;
      font-weight: 600;
      font-size: 1.2rem;
      margin-right: 20px;
    }
  }

  .field {
    padding: 10px 0;
    border-bottom: 1px solid #eee;
  }

  .simple-body {
    margin-top: 10px;
    .desc {
      margin-left: 10px;
      color: #aaa;
    }
  }

  .none {
    margin: 10px 0;
  }
}
</style>
