<template>
  <!-- <Enum v-if="'enum' in field"
        :enums="field.enum" /> -->
  <ObjectType v-if="isObject"
              :title="title"
              :field="field" />
  <ArrayType v-else-if="isArray"
             :type="'item' in field ? field.item : 'object'"
             :field="field[0]" />
  <MapType v-else-if="isMap"
           :field="field" />
</template>

<script>
// import Enum from './Enum.vue';
import ObjectType from './ObjectType.vue';
import ArrayType from './ArrayType.vue';
import MapType from './Map.vue';

export default {
  name: 'ComplexType',
  components: {
    // Enum,
    ObjectType,
    ArrayType,
    MapType,
  },
  props: {
    title: String,
    field: [Object, Array],
  },
  computed: {
    isObject() {
      return this.field.$type === 'object' && !('$key' in this.field);
    },
    isArray() {
      return Array.isArray(this.field);
    },
    isMap() {
      return this.field.$type === 'map';
    },
  },
};
</script>
